@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}







































































.date-range-picker-popup {
  position: relative;
  background-color: #fff;

  &__button {
    position: absolute;
    top: 0;
    padding: 5px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
    font-size: 12px;
    line-height: 1;
    cursor: pointer;
    &:hover {
      background-color: #f2f5fc;
    }
  }

  &__previous {
    left: 0;
  }

  &__next {
    right: 0;
  }

  &__body {
    display: flex;
    align-items: center;
  }

  &__calendar {
    &:not(:first-child) {
      margin-left: 20px;
    }
  }
}
